@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Moirai+One&display=swap');


body {
    margin: 0;
    padding: 0;
}

html {
    margin: 0;
    padding: 0;
}

.App {
    background-color: #000000;
    height: 100vh;
}

#boardwrapper{
    width: 100vw;
    justify-content: center;
    display: flex;
    border: #0C356A 5px;
}

#scoreboardwrapper{
    width: 90vw;
}

#titlewrapper {
    justify-content: center;
    display: flex;
    margin-bottom: 5vh;
    font-family: 'Black Ops One', cursive;
    font-size: 50px;
}

#assemblywrapper {
    justify-content: center;
    display: flex;
    height: fit-content;
    font-family: 'Black Ops One', cursive;
    font-size: 35px;
}

.scorewrapper {
    transition: 1.8s;
    position: absolute;
    display: flex;
    width: 90vw;
    height: 4.5vh;
    border-radius: 2vh;
    font-size: x-large;
    margin-bottom: 2vh;
    border: 2px solid white;
    background-color: rgba(red, green, blue, 0);
    font-family: 'Black Han Sans', sans-serif;
    font-weight: 1000;
    font-size: 30px;
    color: #0033ff;
    text-shadow: 0 0 3px #fff, 0 0 5px #fff, 0 0 6px #fff, 0 0 6px rgb(0, 89, 255),
    0 0 54px rgb(0, 55, 255), 0 0 20px rgb(0, 60, 255), 0 0 23px rgb(0, 115, 255), 0 0 13px rgb(0, 123, 255);
    box-shadow: #5C4B99 5px 5px 5px;
}

.ranking {
    margin-left: 2vw;
}

.name {
    position: absolute;
    left: 10vw;
}

.score {
    position: absolute;
    left: 80vw;
}

#title {
    margin: 0;
}

#assemblytitle {
    margin: 0;
}

#headwrapper {
    top: 3vh;
    position: relative;
    color: #fff;
    animation: flicker 1.5s infinite alternate;
}

@keyframes flicker {
    
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
  
        text-shadow: 0 0 3px #fff, 0 0 5px #fff, 0 0 6px #fff, 0 0 6px rgb(0, 89, 255),
    0 0 54px rgb(0, 55, 255), 0 0 120px rgb(0, 60, 255), 0 0 53px rgb(0, 115, 255), 0 0 43px rgb(0, 123, 255);
    
    }
    
    20%, 24%, 55% {        
        text-shadow: none;
    }    
}

#loader {
    font-family: 'Moirai One', cursive;
    color: #04BEFE;
    text-shadow: 0 0 3px #fff, 0 0 5px #fff, 0 0 6px #fff, 0 0 6px rgb(0, 89, 255),
    0 0 54px rgb(0, 55, 255), 0 0 120px rgb(0, 60, 255), 0 0 53px rgb(0, 115, 255), 0 0 43px rgb(0, 123, 255);
    
}

#background {
    position: absolute;
    top: 0;
}

#contents {
    position: absolute;
    top: 0;
}

#newscore {
    animation: newEffect 1.8s;
}

#willdisappear {
    animation: noneEffect 1.8s;
}


@keyframes newEffect {
    0%{
        opacity: 0;
        transform: translateY(100vh);
    }
    100%{
        opacity: 1;
        transform: translateY(0vh);
    }
}

@keyframes noneEffect {
    0%{
        opacity: 1;
        transform: translateY(0vh);
    }
    100%{
        opacity: 0;
        transform: translateY(100vh);
    }
}